<template>
  <div>
    <skeleton v-if="skeletonLoading" :nums="4" :iheight="'.4rem'"></skeleton>
    <div v-else class="magic-cube">

      <!-- 头部筛选tab -->
      <div class="header" v-if="tabs.length">
        <div class="scroll-view">
          <div class="tab">
            <div
              :class="{active: item.productcatesId === activeTabId}"
              :id="`id${item.productcatesId}`"
              :key="item.productcatesId"
              @click="tabChange(item)"
              class="tab-item"
              v-for="item in tabs"
            >
              <div class="text">{{item.cateName}}</div>
            </div>
          </div>
        </div>
        <div @click="toggleFilterBox" class="right">
          <img alt mode="widthFix" src="@/assets/imgs/magicCube/down.png" />
        </div>
      </div>

      <!-- 筛选浮层 -->
      <filter-box
        :activeTabId="activeTabId"
        :tabs="tabs"
        @tabChange="tabChange"
        @toggleFilterBox="toggleFilterBox"
        v-if="showFilterBox && tabs.length"
      ></filter-box>

      <template v-if="tabs[0] && activeTabId === tabs[0].productcatesId">
        <!-- banner -->
        <banner-swiper @handleClick="handleClick" v-if="magicCubeInfo.bannerList.length" :bannerList="magicCubeInfo.bannerList" />

        <!-- kingkong -->
        <kingkong @handleClick="handleClick" v-if="magicCubeInfo.jinGangList.length" :jinGangList="magicCubeInfo.jinGangList" />

        <!-- 活动区 -->
        <activities @handleClick="handleClick" v-if="magicCubeInfo.activityList.length" :activityList="magicCubeInfo.activityList" />

        <!-- fresh-row -->
        <img class="fresh-row" src="@/assets/imgs/magicCube/fresh-row.png" />
      </template>
      <!-- 商品列表 -->
      <template>
        <div class="products-empty" v-if="!productList.length && fetchEnd">
          <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/home/new/empty.png" alt="">
        </div>
        <public-product-list :productList="productList" :switchStyle='true' :collection='false' @handleGoodClick="handleGoodClick"/>
        <div class="loading-text" v-scroll="scrollFn">
          <div v-if="!fetchEnd">加载中...</div>
          <div v-else>没有更多了</div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import skeleton from '@/components/skeleton'
import filterBox from './components/filterBox'
import bannerSwiper from './components/bannerSwiper'
import kingkong from './components/kingkong'
import activities from './components/activities'
import publicProductList from '@/components/publicProductList'
import { findTopCategory, findMagicCube, getNewProductList } from '@/http/service/magicCube'

export default {
  components: {
    skeleton,
    filterBox,
    bannerSwiper,
    kingkong,
    activities,
    publicProductList
  },
  data() {
    return {
      skeletonLoading: true,

      showFilterBox: false,
      activeTabId: null,
      tabs: [],

      productList: [],
      pageIndex: 1,
      fetching: false,
      fetchEnd: false,
      // magicCubeInfo jumpType[1单品页、3游戏页、4主题活动、5自定义链接-无导航栏、16自定义链接-有导航栏、17智家协议链接、18动态活动、23自定义页面(单点登录)-有导航栏、24自定义页面(单点登录)-无导航栏]
      magicCubeInfo: {
        newProductDays: 0,
        bannerList: [],
        jinGangList: [],
        activityList: []
      }
    }
  },
  mounted() {
    this.initData()
  },
  directives: {
    scroll: {
      bind: function (el, binding) {
        document.getElementById('app-warp').addEventListener('scroll', () => {
          if (el.getBoundingClientRect().top < window.innerHeight + 200) binding.value('fetch')
        })
      }
    }
  },
  methods: {
    handleClick(item) {
      let params = ''
      switch (item.jumpType) {
        case '1':
          params = `productId=${item.productId}`
          break;
        case '3':
          params = `gameId=${item.gameId}`
          break;
        case '4':
        case '18':
          params = `id=${item.activityId}`
          break;
        case '23':
        case '24':
          params = `target=${item.target}&redirectUrl=${item.linkAddress}`
          break;
        default:
          params = item.linkAddress
      }

      this.$store.dispatch('goPageByType', {
        type: Number(item.jumpType),
        params,
      })
    },
    async initData() {
      const res = await Promise.all([findTopCategory(), findMagicCube()])
      if (res[0].data.success && res[1].data.success) {
        this.tabs = [
          {
            productcatesId: 'all',
            cateName: '精选'
          },
          ...res[0].data.data
        ]
        this.activeTabId = 'all'
        this.magicCubeInfo = res[1].data.data
        this.fetchData(true)
      }
    },
    scrollFn(status) {
      if (status === 'fetch' && this.activeTabId) this.fetchData()
    },
    toggleFilterBox() {
      this.showFilterBox = !this.showFilterBox
    },
    tabChange(tabItem) {
      this.$util.gioTrackWithVariable('mall_cube_click_cate', {
        category_name: tabItem.cateName || 'null'
      })
      if (tabItem.productcatesId === this.activeTabId) return
      document.getElementById(`id${tabItem.productcatesId}`).scrollIntoView({
        inline: 'center',
      })

      if (tabItem.productcatesId === 'all') {
        findMagicCube().then(res => {
          this.magicCubeInfo = res.data.data
        })
      }

      this.activeTabId = tabItem.productcatesId
      this.refreshList()
      document.getElementById('app-warp').scroll(0, 0)
    },
    fetchData(flag) {
      if (this.fetching || this.fetchEnd) return
      this.fetching = true
      const params = {
        days: this.magicCubeInfo.newProductDays,
        pageNo: this.pageIndex,
        pageSize: 10,
      }

      if (this.activeTabId === 'all') {
        params.cateIds = this.tabs.slice(1).map(v => v.productcatesId)
      } else {
        params.cateIds = [this.activeTabId]
      }

      if (flag) this.productList = []

      getNewProductList(params).then(res => {
        let result = res.data
        if (result.success) {
          let productList = result.data

          if (productList.length) this.pageIndex += 1
          if (productList.length < 10) this.fetchEnd = true

          this.productList = this.productList.concat(productList)

          this.fetching = false
          if (this.skeletonLoading) this.skeletonLoading = false
        }
      }).catch(() => {
        this.fetchEnd = true
      })
    },
    /**
     * @description 商品点击
    */
    handleGoodClick(item) {
      this.$util.gioTrackWithVariable('mall_cube_click_pro', {
        productId_var: item.productId + ''
      })
    },
    refreshList() {
      this.fetchEnd = false
      this.pageIndex = 1
      this.fetchData(true)
    },
  }
}
</script>

