  width: 100%;
  height: 100%;
  overflow: auto;
  z-index: 10;
  position: relative;
  box-sizing: border-box;
  padding-top: 0.92rem;
  .header {
    display: flex;
    align-items: center;
    height: 0.88rem;
    background-color: #fff;
    border-bottom: 0.02rem solid #EEEEEE;
    position: fixed;
    right: 0;
    left: 0;
    top: 0.92rem;
    z-index: 4;
    .scroll-view {
      height: 0.88rem;
      width: calc(100% - 1rem);
      box-sizing: border-box;
      padding-right: 0.32rem;
      overflow-x: auto;
    }
    .tab {
      display: flex;
      align-items: center;
      padding-left: 0.32rem;
      color: #333;
      .tab-item {
        height: 0.88rem;
        display: flex;
        align-items: center;
        position: relative;
        font-size: 12px;
        margin-right: 0.6rem;
        .text {
          white-space: nowrap;
        }
      }
      .tab-item:last-child {
        margin-right: 0;
      }
      .active {
        font-size: 14px;
        .text {
          font-weight: 600;
        }
      }
    }
    .right {
      height: 0.5rem;
      width: 1rem;
      // border-left: 0.02rem solid #eee;
      box-sizing: border-box;
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        width: 26px;
      }
    }
  }
  .fresh-row {
    width: 178px;
    height: 21px;
    margin: 14px auto 8px;
  }
  .products-empty{
    padding-top: .4rem;
    text-align: center;
    img{
      display: inline-block;
      max-width: 40%;
    }
  }
  .loading-text{
    padding: .2rem 0;
    margin-bottom: 1.2rem;
    text-align: center;
    font-size: .28rem;
    color: #969799;
  }
}
</style>
