import http from '../http'
import url from '../url'
import config from '../../config'

export const findTopCategory = () => {
  return http.request({
    url: url.findTopCategory,
    method: 'post',
    data: {}
  })
}

export const findMagicCube = () => {
  return http.request({
    url: url.findMagicCube,
    method: 'post',
    data: {}
  })
}

export const getNewProductList = (data) => {
  return http.request({
    baseURL: config.SHOST,
    url: url.getNewProductList,
    method: 'post',
    data
  })
}

export default {
  findTopCategory,
  findMagicCube,
  getNewProductList
}
