<template>
  <div class="activities">
    <div class="activity-item" v-for="(item,index) in activitySortableList" :key="item.id" @click="handleClick(item,index)">
      <img class="activity-icon" src="@/assets/imgs/magicCube/icon-activity.png" />
      <div class="activity-name">{{ item.name }}</div>
      <img v-lazy="item.imgUrl"/>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    activityList: {
      type: Array,
      default: () => []
    }
  },
  methods: {
    handleClick(item, index) {
      this.$util.gioTrackWithVariable('mall_cube_click_activity', {
        site_number: index + 1 + '',
        text: item.name || 'null'
      })
      this.$emit('handleClick', item)
    }
  },
  computed: {
    activitySortableList() {
      const list = this.activityList
      return list.sort((pre, cur) => {
        return Number(pre.sort) - Number(cur.sort)
      })
    }
  }
}
</script>

