<template>
  <div @click="toggleFilterBox" @touchmove.prevent class="filter-box">
    <div @click.stop class="content">
      <div class="tit">
        <div class="left">请选择类别</div>
        <img @click="toggleFilterBox" alt class="right" src="@/assets/imgs/magicCube/down.png" />
      </div>
      <ul>
        <li
          :class="{active: item.productcatesId === activeTabId}"
          :key="item.productcatesId"
          @click="tabChange(item)"
          v-for="item in tabs"
        >{{item.cateName}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: ['tabs', 'activeTabId'],
  data() {
    return {}
  },
  mounted() {},
  methods: {
    tabChange(item) {
      this.$emit('tabChange', item)
      this.toggleFilterBox()
    },
    toggleFilterBox() {
      this.$emit('toggleFilterBox')
    },
  },
}
</script>

