<template>
  <div class="kingkong">
    <div class="kingkong-item" v-for="(item,index) in jinGangSortableList" :key="item.id" @click="handleClick(item,index)">
      <img v-lazy="item.imgUrl"/>
      <p>{{ item.name }}</p>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    jinGangList: {
      type: Array,
      default: () => []
    }
  },
  methods: {
    handleClick(item, index) {
      this.$util.gioTrackWithVariable('mall_cube_click_kingKong', {
        site_number: index + 1 + '',
        text: item.name || 'null'
      })
      this.$emit('handleClick', item)
    }
  },
  computed: {
    jinGangSortableList() {
      const list = this.jinGangList
      return list.sort((pre, cur) => {
        return Number(pre.sort) - Number(cur.sort)
      })
    }
  }
}
</script>

