<template>
  <div class="banner-swiper">
    <van-swipe ref='vanSwiper'  :autoplay="3000">
      <van-swipe-item v-for="(item,index) in bannerSortableList" :key="item.id">
        <img @click="handleClick(item,index)" v-lazy="item.imgUrl" />
      </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
import commons from '@/utils/commons.js'
export default {
  props: {
    bannerList: {
      type: Array,
      default: () => []
    }
  },
  mounted() {
    commons.swipeResize(this, 'vanSwiper')
  },
  methods: {
    handleClick(item, index) {
      this.$util.gioTrackWithVariable('mall_cube_click_banner', {
        site_number: index + 1 + ''
      })
      this.$emit('handleClick', item)
    }
  },
  computed: {
    bannerSortableList() {
      const list = this.bannerList
      return list.sort((pre, cur) => {
        return Number(pre.sort) - Number(cur.sort)
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.banner-swiper {
  padding: 12px;
  ::v-deep .van-swipe {
    border-radius: 12px;
    .van-swipe-item {
      width: 350px;
      height: 150px;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
}
</style>
